<template>
  <div class="dialog-container">
    <el-dialog title="添加待处理事项" width="1000px" top="30px" :visible.sync="visible" :close-on-click-modal="false" @close="$emit('update:show', false)"
      :show="show">
      <div class="demand">
        <el-form style="width:950px;margin:0 auto" class="demo-ruleForm" :model="hydjData" :inline="true" size="mini"
          label-width="135px">
          
          <el-form-item label="待处理事项" required>
            <el-input size="mini" type="text" style="width:800px" placeholder="请输入待处理事项" v-model="hydjData.clsxmc"></el-input>
          </el-form-item>

          <el-form-item label="计划完成时间" required>
            <el-date-picker :clearable="false" size="mini" v-model="hydjData.jhwcsj"
              type="date" placeholder="选择日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd" style="width:800px;"></el-date-picker>
          </el-form-item>

          <el-form-item label="责任方" required>
             <el-radio-group v-model="hydjData.sfnbhy" style="width:800px">
                <el-radio label="0">公司</el-radio>
                <el-radio label="1">学校</el-radio>
                <el-radio label="2">第三方</el-radio>
             </el-radio-group>
          </el-form-item>

          <el-form-item label="公司责任人" >
             <el-select v-model="gszrrData" filterable multiple  size="mini" placeholder="请选择公司责任人（可按姓名搜索）" style="width:800px" @change="handleChangeGsry">
              <el-option v-for="(item, index) in JzUserList" :key="index" :label="item.username" :value="item.userid+','+item.username"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="学校责任人" >
            <el-input size="mini" type="text" style="width:800px" placeholder="请填写参会学校部门"  v-model="hydjData.xxzrr"></el-input>
          </el-form-item>
          
          <el-form-item label="第三方责任人" >
            <el-input size="mini" type="text" style="width:800px" placeholder="请填写参会第三方"  v-model="hydjData.dsfzrr"></el-input>
          </el-form-item>

          <el-form-item label="关注人" >
             <el-select v-model="gzrData" filterable multiple  size="mini" placeholder="请选择关注人（可按姓名搜索）" style="width:800px" @change="handleChangeGzr">
              <el-option v-for="(item, index) in userList" :key="index" :label="item.username" :value="item.userid+','+item.username"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      

        <div style="text-align:right;width:100%;margin:10px 0;padding:0 20px;">
          <el-button size="small" type="primary" @click="handleCommit">确认提交</el-button>
          <el-button size="small" @click="visible = false">取消</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        visible: this.show,

        JzUserList:[],
        userList:[],
        gszrrData:[],//公司责任人
        gzrData:[],//关注人
        hydjData: {
         clsxmc:"",
         jhwcsj:"",
         zrf:"",
         gszrrData:"",
         gzrData:"",
         xxzrr:"",
         dsfzrr:""
        },
      };
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      wid:{
        type:String,
        default:''
      }
    },
    components: {},
    methods: {
      // 公司人员
      handleChangeGsry(){
        this.hydjData.gszrrData = val.join('|');
      },
      // 关注人
      handleChangeGzr(){
        this.hydjData.gzrData = val.join('|');
      },
      //提交需求
      handleCommit() {
        this.hydjData.sm = $("#summernoteTT").summernote("code");
        if (!this.valiDate()) return;
        this.$post(this.API.saveServiceDesk, this.hydjData).then(res => {
          if (res.state == "success") {
            this.$message({
              message: "提交成功",
              type: "success"
            });
            $("#summernoteTT").summernote("code", "");
            this.isClearFile = !this.isClearFile;
            this.visible = false;
            this.$emit("handleCommitFwdj",'');
          } else {
            this.$message({
              message: res.msg,
              type: "warning"
            });
          }
        });
      },

    
      //  获取金智用户
      getUsers(unitType) {
        this.$get(this.API.getUsers, {
          curPage: 1,
          pageSize: 9999,
          unitType: unitType,
          keyword: "",
          dept: ""
        }).then(res => {
          if (res.state == "success") {
            if(unitType == '0'){
              this.JzUserList = res.data.rows;
            }else if(!!unitType){
              this.userList = res.data.rows;
            }
          } else {
          }
        });
      },

      valiDate() {
        if (/^[\s]*$/.test(this.hydjData.dclsx)) {
          this.$message({
            message: "请填写待处理事项",
            type: "warning"
          });
          return false;
        }
        if (!this.hydjData.jhwcsj) {
          this.$message({
            message: "请选择计划完成时间",
            type: "warning"
          });
          return false;
        }
        if (!this.hydjData.zrf) {
          this.$message({
            message: "请选择责任方",
            type: "warning"
          });
          return false;
        }
        return true;
      }
    },
    watch: {
      show() {
        this.visible = this.show;
        if (this.show) {

          this.getUsers('0');
          this.getUsers('');
        } else {
         
        }
      }
    }
  };
</script>
<style scoped lang="scss">
  .demand {
    padding: 10px 0;
  }

  div.el-form-item {
    margin-bottom: 8px !important;
  }


  .file {
    padding: 2px 6px;
    border-radius: 3px;
    margin-top: 4px !important;

    &:hover {
      background: rgba(216, 214, 214, 0.5);
    }

    i {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      text-align: center;
      line-height: 20px;

      &:hover {
        color: #f00;
        cursor: pointer;
        background: rgba(255, 0, 0, 0.25);
      }
    }
  }

  .cptree {
    position: absolute;
    top: 0;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    border: 1px solid #e4e7ed;
    border-radius: 4px;
    top: 40px;
    z-index: 10200;
    width: 325px;
    max-height: 400px;
    overflow-y: auto;
    overflow-x: auto;
  }
</style>



